<template>
<div>
    <div class="goods-type">
        <el-tabs @tab-click="getfoods">
            <el-tab-pane v-for="(item,index) in foodtype" :key="index" :label="item.category">
                <ul class="cookList">
                    <li v-for="(goods,index) in typeGoods" :key="index">
                        <span class="foodImg"><img :src="goods.pictureUrl" height="80px" width="100px"></span>
                        <el-button size="mini" @click="deleteGood(goods.goodsId)">删除</el-button>
                        <span class="foodName">{{goods.name}}</span>
                        <span class="foodPrice">￥{{goods.price}}元</span>
                    </li>
                </ul>
            </el-tab-pane>
        </el-tabs>

        <el-tabs>
            <el-tab-pane label="添加类别或商品">
                <el-button type="text" @click="dialogFormVisible1 = true">添加种类</el-button>
                <el-button type="text" @click="dialogFormVisible2 = true">删除种类</el-button>
                <el-button type="text" @click="dialogFormVisible = true">添加商品</el-button>
            </el-tab-pane>
        </el-tabs>

        <el-dialog title="添加种类" :visible.sync="dialogFormVisible1" width="300px">
            <el-form :model="addtypeform">
                <el-form-item label="名称" :label-width="formLabelWidth">
                    <el-input v-model="addtypeform.name" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible1 = false">取 消</el-button>
                <el-button type="primary" @click="addType">确 定</el-button>
            </div>
        </el-dialog>

        <el-dialog title="添加商品" :visible.sync="dialogFormVisible" width="300px">
            <el-form :model="form">
                <el-form-item label="名称" :label-width="formLabelWidth">
                    <el-input v-model="form.name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="价格" :label-width="formLabelWidth">
                    <el-input v-model="form.price" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="种类" :label-width="formLabelWidth">
                    <el-select v-model="form.goodsCategoryId" placeholder="请选择商品种类">
                        <el-option v-for="item in this.foodtype" :key="item.goodsCategoryId" :label="item.category" :value="item.goodsCategoryId"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="图片" :label-width="formLabelWidth">
                    <el-upload class="avatar-uploader" action="http://192.168.43.102:8080/resources/img" :show-file-list="false" :on-success="handleAvatarSuccess">
                        <img v-if="imageUrl" :src="imageUrl" class="avatar">
                      <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="addGood">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</div>
</template>

<script>
import {
    queryGoodsById,
    queryGoodsCategory,
    addGood,
    addGoodsCategory,
    deleteGoodsCategory
} from '../api/goods'
export default {
    data() {
        return {
            options: [],
            value: [],
            list: [],
            imageUrl: 'http://192.168.43.102:8080/resources/img/1.jpg',
            fileList: [],
            typeGoods: [],
            loading: false,
            addtypeform: {
                "name": ''
            },
            dialogFormVisible: false,
            dialogFormVisible1: false,
            formLabelWidth: '60px',
            form: {},
            foodtype: '',
        }
    },
    mounted() {

    },
    methods: {
        handleAvatarSuccess(res, file) {
            this.imageUrl = URL.createObjectURL(file.raw);
        },
        addGood() {
            this.dialogFormVisible = true;
            addGood(this.form).then((res) => {
                if (res.data.status == 1) {
                    this.dialogFormVisible = false;
                    alert("添加成功！")
                } else {
                    alert("添加失败！")
                }
            })
        },
        addType() {
            console.log("添加的种类的名" + this.addtypeform.name)
            this.dialogFormVisible1 = true;
            addGoodsCategory(this.addtypeform).then((res) => {
                if (res.data.status == 1) {
                    this.dialogFormVisible1 = false;
                    alert("添加成功！")
                } else {
                    alert("添加失败！")
                }
            })

        },
        deleteGood(data) {
            deleteGoodsCategory(data).then((res) => {
                if (res.data.status == 1) {
                    alert("删除成功")
                }
            })
        },
        getfoods(tab, event) {
            let type = parseInt(tab.index)
            console.log(type)
            if (type < 0) {
                console.log("正在向后台获取所有桌子...")
            } else {
                if (type <= this.foodtype.length) {
                    console.log("正在向后台获取类型为" + this.foodtype[type].category + "的食品.....")
                    let queryType = this.foodtype[type].goodsCategoryId
                    queryGoodsById(queryType).then((res) => {
                        if (res.data) {
                            this.typeGoods = res.data.goods
                        }
                    })
                }
            }

        },
    },
    created: function () {

        console.log("正在加载商品信息")
        queryGoodsCategory().then((res) => {
            if (res.data) {
                this.foodtype = res.data.goodsCategory
                console.log(this.foodtype)
                let queryType = this.foodtype[0].goodsCategoryId
                queryGoodsById(queryType).then((res) => {
                    if (res.data) {
                        this.typeGoods = res.data.goods
                    }
                })
            }
        })

    }
}
</script>

<style scoped>
.often-goods-list {
    overflow: hidden;
}

.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}

.avatar-uploader .el-upload:hover {
    border-color: #409EFF;
}

.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
}

.avatar {
    width: 178px;
    height: 178px;
    display: block;
}

.often-goods-list ul li {
    list-style: none;
    float: left;
    border: 1px solid #e5e9f2;
    padding: 10px;
    margin: 5px;
    background-color: #fff;
}

.o-price {
    color: #58b7ff;
}

.el-select-dropdown__item {
    color: black;
    background-color: #8c939d;
}

.cookList li {
    list-style: none;
    width: 23%;
    border: 1px solid #e5e9f2;
    height: auot;
    overflow: hidden;
    background-color: #fff;
    padding: 2px;
    float: left;
    margin: 2px;
}

.cookList li span {
    display: block;
    float: left;
}

.foodImg {
    width: 40%;
}

.foodName {
    font-size: 18px;
    padding-left: 10px;
    color: brown;
}

.foodPrice {
    font-size: 16px;
    padding-left: 10px;
    padding-top: 10px;
    color: brown;
}

.el-tabs__content {
    width: 100vw;
}

.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}

.avatar-uploader .el-upload:hover {
    border-color: #409EFF;
}

.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
}

.avatar {
    width: 178px;
    height: 178px;
    display: block;
}

.el-button {
    color: black;
    background-color: #409EFF;
}
</style>



